import React from "react";
import TextField from "@mui/material/TextField";
import { FieldProps } from "formik";
import { Themecolors } from "api/Colors";

type NumericInputProps = FieldProps & {
  label: string;
};

const NumericInput: React.FC<NumericInputProps> = ({
  field,
  form: { touched, errors },
  label,
}) => (
  <TextField
    {...field}
    // label={label}
    variant="outlined"
    type="number"
    error={touched[field.name] && Boolean(errors[field.name])}
    fullWidth
    InputLabelProps={{
      sx: {
        color: Themecolors.InputText_Color1,
        "&.Mui-focused": {
          color: Themecolors.InputText_Color1,
        },
      },
    }}
    sx={{
      "& .MuiOutlinedInput-root": {
        "& .MuiInputBase-input": {
          paddingY: "10px",
        },
        borderRadius: "4px",
        "& fieldset": {
          borderColor: " #bdbdbd",
        },
        "&:hover fieldset": {
          borderColor: Themecolors.InputBorder2,
        },
        "&.Mui-focused fieldset": {
          borderColor: Themecolors.InputBorder2,
        },
      },
    }}
    InputProps={{
      inputProps: { min: 0, step: 0.5 }, // You can set minimum and maximum values if needed
      sx: {
        color: Themecolors.InputText_Color1,
        "& .MuiInputBase-input": {
          color: Themecolors.InputText_Color1,
        },
      },
    }}
  />
);

export default NumericInput;
