import React, { useState } from "react";
import { FieldProps } from "formik";
import Select from "@mui/material/Select";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import InputLabel from "@mui/material/InputLabel";
import { Themecolors } from "api/Colors";

type Option = {
  id: number;
  name: string;
};

type DropdownProps = FieldProps & {
  label: string;
  options: Option[];
};

const Dropdown: React.FC<DropdownProps> = ({
  field,
  form: { touched, errors, setFieldValue },
  label,
  options,
}) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

  const handleOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const isMenuOpenUpwards =
    anchorEl &&
    window.innerHeight - anchorEl.getBoundingClientRect().bottom < 200;

  return (
    <FormControl
      variant="outlined"
      fullWidth
      error={touched[field.name] && Boolean(errors[field.name])}
      sx={{
        "& .MuiOutlinedInput-root": {
          "& .MuiInputBase-input": {
            paddingY: "10px",
          },
          borderRadius: "4px",
          "& fieldset": {
            borderColor: " #bdbdbd",
          },
          "&:hover fieldset": {
            borderColor: Themecolors.InputBorder2,
          },
          "&.Mui-focused fieldset": {
            borderColor: Themecolors.InputBorder2,
          },
        },
      }}
    >
      <Select
        {...field}
        value={field.value ?? ""}
        onChange={(event) => {
          setFieldValue(field.name, event.target.value);
        }}
        onOpen={handleOpen}
        onClose={handleClose}
        displayEmpty
        MenuProps={{
          PaperProps: {
            style: {
              boxShadow: "none",
              opacity: 2,
              maxHeight: "250px",
              backgroundColor: "#fff",
              border: "1px solid #f5f5f5",
              marginTop: isMenuOpenUpwards ? "-5px" : "5px",
            },
          },
          anchorOrigin: {
            vertical: isMenuOpenUpwards ? "top" : "bottom",
            horizontal: "left",
          },
          transformOrigin: {
            vertical: isMenuOpenUpwards ? "bottom" : "top",
            horizontal: "left",
          },
        }}
      >
        {options.map((option) => (
          <MenuItem key={option.id} value={option.id}>
            {option.name}
          </MenuItem>
        ))}
      </Select>

      {touched[field.name] && errors[field.name] && (
        <div style={{ color: "red" }}>{errors[field.name] as any}</div>
      )}
    </FormControl>
  );
};

export default Dropdown;
